@import url('https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap');

* {
    margin: 0;
    padding: 0;
}

.header {
    background: #ffffff center/cover url("/src/assets/backgrounds/header-background.png") no-repeat;
    height: 207px;
    border-bottom: 3px solid #d6ccd1;

    display: flex;
    flex-direction: row;
    align-items: end;
}

.logo {
    margin-top: 10px;
    margin-right: 25px;
}

.verse {
    font-size: 40px;
    font-family: 'Amatic SC', cursive;
    font-weight: 700;
    font-style: italic;
    text-align: left;

    display: flex;
    flex-direction: column;
}

@media only screen and (max-width: 600px) {
    .verse {
        font-size: 24px;
    }

    .header {
        justify-content: space-around;
        align-items: center;
    }
}
